import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core/styles'
import Paper from '@material-ui/core/Paper'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import Typography from '@material-ui/core/Typography'
import Dialog from '@material-ui/core/Dialog'
import DialogActions from '@material-ui/core/DialogActions'
import DialogContent from '@material-ui/core/DialogContent'
import DialogContentText from '@material-ui/core/DialogContentText'
import DialogTitle from '@material-ui/core/DialogTitle'
import TextField from '@material-ui/core/TextField'
import IconButton from '@material-ui/core/IconButton'
import FileCopy from '@material-ui/icons/FileCopyOutlined'
//import Block from '@material-ui/icons/Block'
import QrCodelogo from '@material-ui/icons/DashboardOutlined'
import Checkbox from '@material-ui/core/Checkbox'
import Select from '@material-ui/core/Select'
import InputLabel from '@material-ui/core/InputLabel'
import MenuItem from '@material-ui/core/MenuItem'
import FormControl from '@material-ui/core/FormControl'
import Button from '@material-ui/core/Button'
import Tooltip from '@material-ui/core/Tooltip'
import { ChromePicker } from 'react-color'
import QRCode from 'algorand-qrcode'
import logo from '../assets/images/logo.png'
import algosdk from 'algosdk'
import MyAlgo from '@randlabs/myalgo-connect'
import svg from '../assets/images/qrlogo.svg'
import nextId from 'react-id-generator'
import List from './List'

const styles = (theme) => ({
  root: {
    maxWidth: '60%',
    margin: 'auto',
    padding: theme.spacing(3),
    marginTop: theme.spacing(5),
    marginBottom: theme.spacing(3)
  },
  title: {
    margin: `${theme.spacing(3)}px 0 ${theme.spacing(2)}px`,
    color: '#333333',
    textAlign: 'center',
    fontSize: '1.2em',
    fontWeight: 'bold'
  },
  textField: {
    width: '100%'
  },
  addressField: {
    minWidth: '90%',
    width: '100%',
    verticalAlign: 'middle'
  },
  formControl: {
    width: '100%'
  },
  colorSelect: {
    width: '40%',
    marginRight: 50
  },
  generateBtn: {
    borderRadius: 3,
    textAlign: 'center',
    margin: 'auto',
    marginLeft: '15px',
    display: 'inline-block',
    backgroundColor: 'rgb(115, 175, 245)',
    borderColor: 'rgb(115, 175, 245)',
    color: '#fff',
    cursor: 'pointer',
    '&:hover': {
      backgroundColor: '#fff',
      color: 'rgb(115, 175, 245)'
    }
  },
  walletContainer: {
    width: '100%',
    verticalAlign: 'middle',
    textAlign: 'center'
  },
  qrBtn: {
    borderRadius: 3,
    width: '32px',
    height: '32px',
    padding: '3px',
    textAlign: 'center',
    backgroundRepeat: 'no-repeat',
    margin: 'auto',
    cursor: 'pointer',
    marginLeft: '15px',
    display: 'inline-block',
    backgroundImage: `url(${svg})`,
    borderColor: 'rgb(115, 175, 245)',
    backgroundColor: 'rgb(115, 175, 245)',
    color: '#fff',
    verticalAlign: 'middle',
    '&:hover': {
      backgroundColor: 'rgb(90, 145, 245)',
      color: '#fff'
    }
  },
  myAlgoBtn: {
    borderRadius: 3,
    width: '32px',
    height: '32px',
    padding: '3px',
    textAlign: 'center',

    margin: 'auto',
    cursor: 'pointer',
    marginLeft: '15px',
    display: 'inline-block',

    borderColor: 'rgb(115, 175, 245)',
    backgroundColor: 'rgb(115, 175, 245)',
    color: '#fff',
    verticalAlign: 'middle',
    '&:hover': {
      backgroundColor: 'rgb(90, 145, 245)',
      color: '#fff'
    }
  },
  qrcodeDiv: {
    borderbottom: '10px solid rgb(115, 175, 245)',
    border: '1px solid #333',
    margin: 'auto',
    display: 'block',
    padding: 10
  },
  qrImage: {
    borderRadius: '50px',
    width: '256px',
    height: '256px',
    margin: 'auto',
    display: 'block',
    [theme.breakpoints.down('xs')]: {
      width: '100px',
      height: '100px'
    }
  },
  assetUrl: {
    [theme.breakpoints.down('xs')]: {
      wordWrap: 'break-word'
    }
  },
  margin: {
    margin: theme.spacing(3)
  },
  textDiv: {
    margin: 'auto',
    display: 'block',
    backgroundColor: 'rgb(115, 175, 245)',
    borderColor: 'rgb(115, 175, 245)'
  },
  subTitle: {
    padding: '10px',
    margin: 'auto',
    display: 'block'
  },
  uri: {
    padding: '10px',
    margin: 'auto',
    display: 'block'
  },
  right: {
    float: 'right'
  },
  dialog: {
    wordWrap: 'break-word',
    textAlign: 'center'
  },
  modal: {
    width: '40%',
    minWidth: '300px',
    padding: theme.spacing(3),
    overflowY: 'auto',
    '&::-webkit-scrollbar': {
      width: '5px'
    },
    '&::-webkit-scrollbar-track': {
      boxShadow: 'inset 0 0 5px grey',
      borderRadius: '10px'
    },
    '&::-webkit-scrollbar-thumb': {
      background: 'gray',
      borderRadius: '10px'
    },
    '&::-webkit-scrollbar-thumb:hover': {
      background: 'gray'
    },
    scrollbarColor: 'gray',
    scrollbarWidth: 'thin'
  }
})
class AssetExample extends Component {
  constructor() {
    super()
    this.state = {
      wallet: 'LP6QRRBRDTDSP4HF7CSPWJV4AG4QWE437OYHGW7K5Y7DETKCSK5H3HCA7Q',
      label: 'Your amazing wallet name',
      note: '',
      xnote: false,
      html: false,
      amount: 1,
      asset: '15993946',
      inverse: false,
      margin: 4,
      uri: 'algorand://',
      walletUri: 'algorand://',
      errorLevel: 'high',
      version: 'auto',
      dataURL: logo,
      walletDataURL: logo,
      showOptIn: false,
      isWalletQrModalOpen: false,
      isAssetQrModalOpen: false,
      isCheckAsset: false,
      optInList: [],
      assetOK: false,
      walletOK: false,
      isWalletOptInAsset: false,
    }
    this.generateQRCode = this.generateQRCode.bind(this)
    this.generateWalletQRCode = this.generateWalletQRCode.bind(this)
    this.handleClose = this.handleClose.bind(this)
    this.myAlgoWallet = null
    this.fetchAsset = this.fetchAsset.bind(this)
    this.fetchWallet = this.fetchWallet.bind(this)
    this.fetchAssetOptIns = this.fetchAssetOptIns.bind(this)
    this.checkAssetOptIn = this.checkAssetOptIn.bind(this)
    this.optInAsset = this.optInAsset.bind(this)
    this.myAlgoConnect = this.myAlgoConnect.bind(this)
  }

  componentDidMount() {
    this.fetchAsset()
  }
  handleClose() {
    this.setState({
      isWalletQrModalOpen: false,
      isAssetQrModalOpen: false,
      isCheckAsset: false
    })
  }
  fetchAsset() {
    const { asset } = this.state
    const url = `https://api.testnet.algoexplorer.io/v2/assets/${asset}`
    window
      .fetch(url, {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json'
        }
      })
      .then((response) => response.json())
      .then((data) => {
        if (data) {
          if (data.params) {
            return this.setState({
              assetOK: true,
              assetName: data.params.name,
              assetURL: data.params.url,
              assetUnit: data.params['unit-name']
            })
          }
        }
        return this.setState({
          assetOK: false,
          assetName: 'No asset found!',
          assetURL: 'https://',
          assetUnit: 'N/A'
        })
      })
      .catch((error) => {
        console.error('Error:', error)
      })
  }
  fetchWallet() {
    const { wallet } = this.state
    const url = `https://api.testnet.algoexplorer.io/v2/accounts/${wallet}`
    window
      .fetch(url, {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json'
        }
      })
      .then((response) => response.json())
      .then((data) => {
        if (data) {
          if (data.params) {
            return this.setState({
              assetOK: true,
              assetName: data.params.name,
              assetURL: data.params.url,
              assetUnit: data.params['unit-name']
            })
          }
        }
        return this.setState({
          assetOK: false,
          assetName: 'No asset found!',
          assetURL: 'https://',
          assetUnit: 'N/A'
        })
      })
      .catch((error) => {
        console.error('Error:', error)
      })
  }
  fetchAssetOptIns() {
    const { asset } = this.state
    const self = this
    const optInTrxs = []
    const url = `https://testnet.algoexplorerapi.io/idx2/v2/transactions?asset-id=${asset}&tx-type=axfer`
    window
      .fetch(url, {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json'
        }
      })
      .then((response) => response.json())
      .then((data) => {
        if (data) {
          if (data.transactions) {
            data.transactions.forEach((trx) => {
              if (trx['asset-transfer-transaction']) {
                if (trx.sender === trx['asset-transfer-transaction'].receiver && trx['asset-transfer-transaction'].amount === 0) {
                  optInTrxs.push(trx)
                }
              }
            })
            self.setState({ optInList: optInTrxs })
          }
        }
      })
      .catch((error) => {
        console.error('Error:', error)
      })
  }
  checkAssetOptIn() {
    const { asset, wallet } = this.state
    const self = this
    let isOptIn = false
    const url = `https://testnet.algoexplorerapi.io/idx2/v2/transactions?asset-id=${asset}&tx-type=axfer&address=${wallet}`
    window
      .fetch(url, {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json'
        }
      })
      .then((response) => response.json())
      .then((data) => {
        if (data) {
          if (data.transactions) {
            data.transactions.forEach((trx) => {
              if (trx['asset-transfer-transaction']) {
                if (trx.sender === trx['asset-transfer-transaction'].receiver && trx['asset-transfer-transaction'].amount === 0) {
                  isOptIn = true
                }
              }
            })
            self.setState({ isWalletOptInAsset: isOptIn })
          }
        }
      })
      .catch((error) => {
        console.error('Error:', error)
      })
  }
  async optInAsset() {
    const self = this
    try {
      this.myAlgoWallet =
        this.myAlgoWallet === null ? new MyAlgo() : this.myAlgoWallet
      const accounts = await this.myAlgoWallet.connect()
      if (accounts[0]) {
        if (accounts[0].address) {
          self.setState({ wallet: accounts[0].address })
          const algodClient = new algosdk.Algodv2(
            '',
            'https://api.testnet.algoexplorer.io',
            ''
          )
          const params = await algodClient.getTransactionParams().do()
          params.fee = 1000
          params.flatFee = true
          const sender = accounts[0].address
          const recipient = sender

          const amount = 0
          const txn = {
            ...params,
            from: sender,
            type: 'axfer',
            to: recipient,
            assetIndex: Number(self.state.asset),
            amount: amount,
            note: new Uint8Array(Buffer.from(self.state.note))
          }

          const signedTxn = await this.myAlgoWallet.signTransaction(txn)
          console.log(signedTxn)

          self.setState({ note: JSON.stringify(signedTxn) })
          await algodClient.sendRawTransaction(signedTxn.blob).do()
        }
      }
    } catch (err) {
      console.error(err)
    }
  }

  generateQRCode() {
    const {
      wallet,
      note,
      xnote,
      html,
      amount,
      asset,
      inverse,
      version,
      margin,
      errorLevel,
      lightColor,
      darkColor
    } = this.state
    const errorCorrectionLevel = errorLevel
    const color = { light: lightColor, dark: darkColor }

    this.fetchAsset()

    const opts = {
      wallet,
      note,
      xnote,
      html,
      amount,
      asset,
      inverse,
      version,
      margin,
      errorCorrectionLevel,
      color
    }
    const self = this
    opts.mode = 'Auto'
    QRCode.toDataURL(null, opts)
      .then((res) => {
        self.setState({ dataURL: res[0], uri: res[1], showOptIn: true })
      })
      .catch((err) => {
        console.error(err)
      })
  }
  generateWalletQRCode() {
    const {
      wallet,
      label,
      inverse,
      version,
      margin,
      errorLevel,
      lightColor,
      darkColor
    } = this.state
    const errorCorrectionLevel = errorLevel
    const color = { light: lightColor, dark: darkColor }

    const opts = {
      wallet,
      label,
      inverse,
      version,
      margin,
      errorCorrectionLevel,
      color
    }
    const self = this
    opts.mode = 'Auto'
    QRCode.toDataURL(null, opts)
      .then((res) => {
        self.setState({ walletDataURL: res[0], walletUri: res[1] })
      })
      .catch((err) => {
        console.error(err)
      })
  }

  async myAlgoConnect() {
    const self = this
    try {
      this.myAlgoWallet =
        this.myAlgoWallet === null ? new MyAlgo() : this.myAlgoWallet
      const accounts = await this.myAlgoWallet.connect()
      console.log(accounts[0])
      self.setState({ wallet: accounts[0].address })
    } catch (err) {
      console.error(err)
    }
  }

  render() {
    const { classes } = this.props
    const { wallet, isWalletQrModalOpen, assetOK, walletOK, asset, isCheckAsset, optInList } =
      this.state

    const versionOptionsArray = []
    versionOptionsArray.push({ value: 'auto', label: 'auto' })
    for (let i = 0; i < 41; i++) {
      versionOptionsArray.push({ value: `${i}`, label: `${i}` })
    }
    const self = this
    return (
      <div>
        <Dialog
          className={classes.dialog}
          open={isWalletQrModalOpen}
          onClose={this.handleClose}
          aria-labelledby='alert-dialog-title'
          aria-describedby='alert-dialog-description'
        >
          <DialogTitle id='alert-dialog-title'>
            {'Algorand Wallet: '}
          </DialogTitle>
          <DialogContent>
            <DialogContentText id='alert-dialog-description'>
              {`${wallet}`}
            </DialogContentText>
            <img
              alt='Algorand QRCode'
              className={classes.qrImage}
              src={this.state.walletDataURL}
            />
          </DialogContent>
          <DialogActions>
            <Button onClick={this.handleClose} color='primary' autoFocus>
              Close
            </Button>
          </DialogActions>
        </Dialog>
        <Dialog
          className={classes.dialog}
          open={isCheckAsset}
          onClose={this.handleClose}
          aria-labelledby='alert-dialog-title'
          aria-describedby='alert-dialog-description'
        >
          <DialogTitle id='alert-dialog-title'>{'Addresses Opted into asset: '}</DialogTitle>
          <DialogContent>
          <List list={optInList} />
          </DialogContent>
          <DialogActions>
            <Button onClick={this.handleClose} color='primary' autoFocus>
              Close
            </Button>
          </DialogActions>
        </Dialog>
        <Paper className={classes.root} elevation={4}>
          <Typography type='title' className={classes.title}>
            Algorand QR Code Asset Example
          </Typography>

          <div className={classes.walletContainer}>
            <TextField
              id='wallet'
              label='Wallet address'
              variant='outlined'
              value={wallet}
              className={classes.addressField}
              margin='normal'
              onChange={(event) => {
                this.setState({ wallet: event.target.value })
              }}
              InputProps={{
                endAdornment: (
                  <>
                    <Tooltip title='Copy wallet address'>
                      <IconButton
                        onClick={() => {
                          var copyText = document.getElementById('wallet')
                          copyText.select()

                          document.execCommand('copy')
                          alert('Copied the wallet address: ' + copyText.value)
                        }}
                        edge='end'
                      >
                        <FileCopy />
                      </IconButton>
                    </Tooltip>
                    <Tooltip title='Show qr code address'>
                      <IconButton
                        onClick={() => {
                          self.generateWalletQRCode()
                         
                          self.setState({
                            isWalletQrModalOpen: true
                          })
                        }}
                        edge='end'
                      >
                        <QrCodelogo />
                      </IconButton>
                    </Tooltip>
                    <Tooltip title='Get address from MyAlgo wallet'>
                      <IconButton
                        id='wallet-connect-button'
                        onClick={this.myAlgoConnect}
                        edge='end'
                      >
                        {walletOK ? (
                        <img
                          width={32}
                          height={32}
                          style={{
                            backgroundColor: '#ddf4dd',
                            padding: 5,
                            borderRadius: 50
                          }}
                          src={logo}
                        />
                      ) : (
                        <img
                          width={32}
                          height={32}
                          style={{                            
                            padding: 5,
                            borderRadius: 50
                          }}
                          src={logo}
                        />
                      )}
                      </IconButton>
                    </Tooltip>
                  </>
                )
              }}
            />
          </div>

          <TextField
            id='amount'
            label='Amount'
            value={this.state.amount}
            variant='outlined'
            className={classes.textField}
            type='number'
            onChange={(event) => {
              this.setState({ amount: event.target.value })
            }}
            InputProps={{ inputProps: { min: 0, max: 100 } }}
            margin='normal'
          />
          <TextField
            required
            id='asset'
            label='Asset ID (Numeric)'
            variant='outlined'
            className={classes.textField}
            onChange={(event) => {
              const asset = event.target.value
              if (asset) {
                if (Number(asset) >= 0) {
                  this.fetchAsset()
                }
              }
              this.setState({ asset: `${asset}` })
            }}
            type='number'
            value={asset}
            margin='normal'
            InputProps={{
              endAdornment: (
                <>
                  <Tooltip title='Copy assest id'>
                    <IconButton
                      onClick={() => {
                        var copyText = document.getElementById('asset')
                        copyText.select()

                        document.execCommand('copy')
                        alert('Copied the asset ID: ' + copyText.value)
                      }}
                      edge='end'
                    >
                      <FileCopy />
                    </IconButton>
                  </Tooltip>
                  <Tooltip title={assetOK ?'Asset ID is valid!' : 'Check asset!'}>
                    <IconButton
                      id='asset-check-button'
                      onClick={() => {
                        self.fetchAssetOptIns()
                        self.setState({
                          isCheckAsset: true
                        })
                      }}
                      edge='end'
                    >
                      {assetOK ? (
                        <img
                          width={32}
                          height={32}
                          style={{
                            backgroundColor: '#ddf4dd',
                            padding: 5,
                            borderRadius: 50
                          }}
                          src={logo}
                        />
                      ) : (
                        <img
                          width={32}
                          height={32}
                          style={{                            
                            padding: 5,
                            borderRadius: 50
                          }}
                          src={logo}
                        />
                      )}
                    </IconButton>
                  </Tooltip>
                  <Dialog
                    className={classes.dialog}
                    open={isWalletQrModalOpen}
                    onClose={this.handleClose}
                    aria-labelledby='alert-dialog-title'
                    aria-describedby='alert-dialog-description'
                  >
                    <DialogTitle id='alert-dialog-title'>
                      {'Algorand Wallet: '}
                    </DialogTitle>
                    <DialogContent>
                      <DialogContentText id='alert-dialog-description'>
                        {`${this.state.wallet}`}
                      </DialogContentText>
                      <img
                        alt='Algorand QRCode'
                        className={classes.qrImage}
                        src={this.state.walletDataURL}
                      />
                    </DialogContent>
                    <DialogActions>
                      <Button
                        onClick={this.handleClose}
                        color='primary'
                        autoFocus
                      >
                        Close
                      </Button>
                    </DialogActions>
                  </Dialog>
                </>
              )
            }}
          />
          <TextField
            id='note'
            label='Note'
            variant='outlined'
            className={classes.textField}
            onChange={(event) => {
              this.setState({ note: event.target.value })
            }}
            value={this.state.note}
            margin='normal'
          />
          <FormControlLabel
            control={
              <Checkbox
                // checked={state.checkedA}
                // onChange={handleChange} name="xNote"
                onChange={(event) => {
                  this.setState({ xnote: event.target.value })
                }}
              />
            }
            id='xNote'
            label='XNote'
          />
          <FormControlLabel
            control={
              <Checkbox
                // checked={state.checkedA}
                // onChange={handleChange} name="xNote"
                onChange={(event) => {
                  this.setState({ inverse: event.target.value })
                }}
              />
            }
            id='inverse'
            label='Inverse'
          />
          <FormControlLabel
            control={
              <Checkbox
                onChange={(event) => {
                  this.setState({ html: event.target.value })
                }}
              />
            }
            id='html'
            label='HTML Escaping'
          />
          <FormControl variant='outlined' className={classes.formControl}>
            <InputLabel id='version'>Version</InputLabel>
            <Select
              id='version'
              value={this.state.version}
              onChange={(event) => {
                this.setState({ version: event.target.value })
              }}
              label='Version'
            >
              {versionOptionsArray.map((version) => {
                return (
                  <MenuItem key={nextId()} selected value={version.value}>
                    {version.label}
                  </MenuItem>
                )
              })}
            </Select>
          </FormControl>

          <TextField
            id='margin'
            label='Margin'
            variant='outlined'
            className={classes.textField}
            type='number'
            InputProps={{ inputProps: { min: 4 } }}
            value={this.state.margin}
            margin='normal'
            onChange={(event) => {
              this.setState({ margin: event.target.value })
            }}
          />
          <FormControl variant='outlined' className={classes.formControl}>
            <InputLabel id='version'>Error Correction</InputLabel>
            <Select
              id='errorLevel'
              value={this.state.errorLevel}
              // onChange={handleChange}
              label='Error Correction'
              onChange={(event) => {
                this.setState({ errorLevel: event.target.value })
              }}
            >
              <MenuItem value='low'>Low</MenuItem>
              <MenuItem value='medium'>Medium</MenuItem>
              <MenuItem value='quartile'>Quartile</MenuItem>
              <MenuItem selected value='high'>
                High
              </MenuItem>
            </Select>
          </FormControl>

          <div className={classes.margin} />
          <FormControl variant='outlined' className={classes.colorSelect}>
            <ChromePicker
              icker
              name='Light color'
              id='lightColor'
              defaultValue='Light color'
              color={this.state.lightColor}
              onChangeComplete={(color) => {
                this.setState({ lightColor: color.hex })
              }}
            />
          </FormControl>

          <FormControl variant='outlined' className={classes.colorSelect}>
            <ChromePicker
              name='Dark color'
              id='darkColor'
              defaultValue='Dark color'
              color={this.state.darkColor}
              onChangeComplete={(color) => {
                this.setState({ darkColor: color.hex })
              }}
            />
          </FormControl>
          <div className={classes.margin} />
          <Button
            id='generate-button'
            variant='outlined'
            onClick={this.generateQRCode}
            className={classes.generateBtn}
          >
            Create Algorand QR Code
          </Button>
          {this.state.showOptIn && (
            <Button
              id='generate-button'
              variant='outlined'
              onClick={this.optInAsset}
              className={classes.generateBtn}
            >
              Opt-in Asset
            </Button>
          )}

          <div className={classes.margin} />
          <div className={classes.qrcodeDiv}>
            <div className='card'>
              <img className={classes.qrImage} src={this.state.dataURL} />
              <div className='container'>
                <h4>
                  <b>{this.state.assetName}</b>
                </h4>
                <p className={classes.assetUrl}>{this.state.assetURL}</p>
                <p>{this.state.assetUnit}</p>
              </div>
            </div>
          </div>
        </Paper>
      </div>
    )
  }
}

AssetExample.propTypes = {
  classes: PropTypes.objectOf(PropTypes.string).isRequired
}
export default withStyles(styles)(AssetExample)
