import React from 'react';
import Link from '@material-ui/core/Link'
import Check from '@material-ui/icons/Check'
const ListItem = ({
  listItem,
  index,
  listLength,
}) => {
  const assetTransferTransaction = listItem['asset-transfer-transaction'];
  return (
    <li className="d-flex">
      <Link target="_blank" href={`https://testnet.algoexplorer.io/address/${assetTransferTransaction.receiver}`}>{assetTransferTransaction.receiver}</Link>
     {(<Check />)}
     
     
    </li>
  );
};

export default ListItem;
